.subheadingContainer_IX {
    align-self: stretch;
    font-family: var(--app-text-main-font);
    display: flex;
    flex-direction: row;
    width: 100%;
    padding-top: 16px;
    padding-left: 10px;
    justify-content: center;
    align-items: center;

    .padH50 {
        padding: 0 50px 0 50px !important;
    }
    .centered {
        text-align: center;
        justify-content: center;
        align-items: center;
    }

    p {
        margin: 0px 0 16px 0;
        color: var(--app-color-gray-light);
        font-family: var(--app-text-main-font);
        font-size: 1rem;
        line-height: 1.3rem;
        text-align: left;
        padding: 0 0 0 0;
    }
}

.subheading_IX {
    font-family: var(--app-text-main-font);
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    color: var(--app-color-gray-light);
    padding-bottom: 15px;
}

.inputStyle_IX {
    font-family: var(--app-text-main-font);
    font-size: 16px;
    color: var(--app-color-gray-dark);
    border-radius: 8px;
    border: 1px solid var(--app-color-gray-lighter) !important;
    width: 100%;
    outline: none;
    height: 56px;
    padding: 0 10px 0 10px;
    // border: none; /* 1px solid currentColor; */
    // -webkit-text-fill-color: currentColor;
    // -webkit-box-shadow: 0 0 0px 1000px white inset;
    // box-shadow: 0 0 0px 1000px white inset;
    // transition: background-color 5000s ease-in-out 0s;
}
.menuOverlayList_IX{
    left:0;
    right:0;
    margin-top: 5px;
}
.menuOverlayListItem_IX{
    padding-left:5px;
}
.tutorialWrapper_IX {
    position: absolute;
    top: 1.5em;
    right: -68.5px;
    height: 62px;
    width: 137px;
    border-radius: 8px;
    background-color: #ffffff;
    box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.1);
}
.tutorialContainer_IX {
    margin: 12px;
    cursor: pointer;
    padding: 0 10px 0 10px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.tutorialImg_IX {
    width: 11.58px;
    height: 15.98px;
}
.tutorialTextContainer_IX {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}
.tutorialText_IX {
    margin: 0 0 0 0;
    height: 24px;
    width: 61px;
    color: var(--app-color-gray-dark);
    font-family:  var(--app-text-main-font);
    font-size: 1rem;
    line-height: 1.3rem;
    text-align: center;
}
.tutorialDuration_IX {
    margin: 0 0 0 0;
    height: 24px;
    width: 61px;
    color: var(--app-color-gray-dark);
    font-family:  var(--app-text-main-font);
    font-size: 0.75rem;
    line-height: 1rem;
    text-align: center;
}
.tutorialStyle_IX {
    z-index: 1;
    cursor: grab;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    color: var(--app-color-gray-dark);
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
